@mixin respond($breakpoint) {
    @if $breakpoint == x-large {
        @media (max-width: 1200px) {
            @content; } }
    @if $breakpoint == large {
        @media (max-width: 900px) {
            @content; } }
    @if $breakpoint == medium {
        @media (max-width: 768px) and (min-width: 639px) {
            @content; } }
    @if $breakpoint == phone {
        @media (max-width: 638px) and (min-width: 300px) {
            @content; } }
    @if $breakpoint == xx-large {
        @media (min-width: 1800px) {
            @content; } } }
