.body {
    font-family: Montserrat sans-serif;
    font-weight: 600;
    color: $color-grey;
    letter-spacing: -0.028rem;
    text-transform: uppercase; }

.section-about {
    &__background-text {
        color: $color-white;
        font-family: Anton;
        font-size: 10rem;
        opacity: 0.07;
        text-transform: uppercase;
        transform: skewY(-5deg) rotate(-15deg) scale(1.5);
        position: absolute;
        top: 15rem;
        left: 20rem;
        text-shadow: $color-white 1px 0 10px;
        z-index: 3;
        @include respond(phone) {
            display: none; } } }
