*, ::after, ::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

html {
    font-size: 100%;
    @include respond(x-large) {
        font-size: 80%; }
    @include respond(large) {
        font-size: 60%; }
    @include respond(medium) {
        font-size: 50%; }
    @include respond(phone) {
        font-size: 40%; }
    @include respond(xx-large) {
        font-size: 100%; } }

.container {
    background: $color-white;
    max-width: 100vw; }
.color-purl {
    color: $color-purl; }
